.carouselCourse::after {
    content: "";
    display: block;
    position: relative;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: linear-gradient(
      160deg,
      var(--main-color) 100%,
      var(--second-color) 0%
    );
    opacity: 0.6;
    z-index: 3;
  }

.carouselCourse{
    /*creates a carousel*/
    padding-top:5px;
    padding-bottom:0px;
}

.carouselItem{
    width: 100%;
    height: 500px;
}

.carouselImage{
    width: 100%;
    margin: auto;
    opacity: .4;
}

.carouselCaption{
    transform: translateY(-5%);
}
.carouselCaption h1{
    font-size: 36px;
}
.carouselCaption p{
    font-size: 28px;
}

